/* Tooltip container */
.Tooltip_tooltip__AcRrp {
  position: relative;
  /* display: inline-block; */
  /* border-bottom: 1px dotted black; */
}

/* Tooltip text */
.Tooltip_tooltip__AcRrp .Tooltip_tooltiptext__2NUeE {
  visibility: hidden;
  width: 40em;
  top: 1.4em;
  left: 1em;
  background-color: #78c2ad;
  color: #343a40;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;
  opacity: 0.9;
  position: absolute;
  z-index: 1;
}

/* Show the tooltip text when you mouse over the tooltip container */
.Tooltip_tooltip__AcRrp:hover .Tooltip_tooltiptext__2NUeE {
  visibility: visible;
}

